import "./midiDevicesDialog.scss";

import Button from "@mui/material/Button";
import Avatar from "@mui/material/Avatar";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import DialogTitle from "@mui/material/DialogTitle";
import Dialog from "@mui/material/Dialog";
import React from "react";

const MidiDevicesDialog = ({
  open,
  setOpen,
  setMidiOutputIndex,
  setMidiOutputName,
  midiOutputsNames,
  setIsMidiEnabeled,
}) => {
  const handleListItemClick = (index, midiOutputName) => {
    setOpen(false);
    setMidiOutputIndex(index);
    setMidiOutputName(midiOutputName);
    setIsMidiEnabeled(true);
  };
  return (
    <Dialog open={open}>
      <DialogTitle>Select MIDI Output</DialogTitle>
      <List sx={{ pt: 0 }}>
        {midiOutputsNames.map((midiOutputName, index) => (
          <ListItem disableGutters>
            <ListItemButton
              onClick={() => handleListItemClick(index, midiOutputName)}
              key={index}
            >
              <ListItemText primary={midiOutputName} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Dialog>
  );
};

export default MidiDevicesDialog;
