import "./main.scss";

import React, { useState } from "react";
import OutputSetting from "./outputSetting/OutputSetting";
import MidiSliders from "./midiSliders/MidiSliders";

const Main = () => {
  const [midiOutputIndex, setMidiOutputIndex] = useState(0);
  const [isMidiEnabeled, setIsMidiEnabeled] = useState(false);

  return (
    <div className="main-container">
      <OutputSetting
        setIsMidiEnabeled={setIsMidiEnabeled}
        setMidiOutputIndex={setMidiOutputIndex}
      ></OutputSetting>
      <MidiSliders
        midiOutputIndex={midiOutputIndex}
        isMidiEnabeled={isMidiEnabeled}
      />
    </div>
  );
};

export default Main;
