import React from "react";
import "./midiSlider.scss";
import conf from "../../../../conf";
import { WebMidi } from "webmidi";
import { Box, Slider } from "@mui/material";

const MidiSlider = ({ midiCC, midiOutputIndex, isMidiEnabeled }) => {
  const onSliderChange = (event, newValue) => {
    if (isMidiEnabeled) {
      WebMidi.outputs[midiOutputIndex].sendControlChange(
        midiCC,
        Math.round(Math.round(newValue))
      );
    }
  };

  return (
    <div className="slider-container">
      <Slider
        min={conf.MIDI_MIN_VALUE}
        max={conf.MIDI_MAX_VALUE}
        valueLabelDisplay="auto"
        orientation="vertical"
        onChange={onSliderChange}
      ></Slider>
      <p>{conf.MIDI_CC_DICT[midiCC]}</p>
    </div>
  );
};

export default MidiSlider;
