import "./outputSetting.scss";

import React, { useState } from "react";
import Button from "@mui/material/Button";
import Slider from "@mui/material/Slider";
import Box from "@mui/material/Box";
import { WebMidi } from "webmidi";
import MidiDevicesDialog from "./midiDevicesDialog/MidiDevicesDialog";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

const OutputSetting = ({ setMidiOutputIndex, setIsMidiEnabeled }) => {
  const [midiOutputsNames, setMidiOutputsNames] = useState([]);
  const [midiOutputName, setMidiOutputName] = useState("Not selected");

  const [dialogOpen, setDialogOpen] = useState(false);

  const handleClickOpen = () => {
    WebMidi.enable()
      .then(() => {
        setMidiOutputsNames(WebMidi.outputs.map((device) => device.name));
      })
      .catch((err) => {
        console.log({ err });
      });
    setDialogOpen(true);
  };

  return (
    <div className="output-container">
      <p>MIDI Out:</p>
      <Button onClick={handleClickOpen}>{midiOutputName}</Button>
      <MidiDevicesDialog
        open={dialogOpen}
        setOpen={setDialogOpen}
        setMidiOutputIndex={setMidiOutputIndex}
        midiOutputsNames={midiOutputsNames}
        setMidiOutputName={setMidiOutputName}
        setIsMidiEnabeled={setIsMidiEnabeled}
      />
    </div>
  );
};

export default OutputSetting;
