import React from "react";
import "./midiSliders.scss";
import conf from "../../../conf";
import MidiSlider from "./midiSlider/MidiSlider";

const MidiSliders = ({ midiOutputIndex, isMidiEnabeled }) => {
  const midiCCs = [1, 11];

  return (
    <div className="sliders-container">
      {midiCCs.map((midiCC) => (
        <MidiSlider
          midiCC={midiCC}
          midiOutputIndex={midiOutputIndex}
          isMidiEnabeled={isMidiEnabeled}
        />
      ))}
    </div>
  );
};

export default MidiSliders;
